.consult_list {
    border-collapse: collapse;
    margin-top: 10px;
    margin-bottom: 15px;
    width: 100%;
}

.consult_list>thead {
    border-collapse: collapse;
    border-bottom: 1px solid rgba(34, 33, 33, 0.815);
    border-top: 1px solid rgba(34, 33, 33, 0.815);
    border-width: 2px;
    border-left: none;
    border-right: none;
}

.consult_list>tbody {

    border-bottom: 2px solid whitesmoke;
}

.consult_status {
    border: none;
    border-radius: 52px;
    font-size: 13px;
    background: rgba(73, 73, 73);
    color: white;
    text-align: center;
}

.innerTable {
    margin: 0 auto;
    /*    margin . */
    width: 100%;
}


.innerTable>thead>tr {
    margin: 0 auto;
    /*    margin . */
    width: 100%;
    border-bottom: 1px solid rgb(105, 105, 105);
}

.innerTable>tbody>tr {
    margin: 0 auto;
    /*    margin . */
    width: 100%;
    border-bottom: 1px solid whitesmoke;
}

.q {
    position: relative;
}

.q .circleBefore::before {
    content: 'Q';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 24px;
    height: 24px;
    background-color: #FF7787; /*   */
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white; /*    . */
    font-weight: bold; /*    . */
}

.txt_qna {
    margin-left: 25px;
}


.a {
    position: relative;
}

.a .circleBefore2::before {
    content: 'A';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 24px;
    height: 24px;
    background-color: #77d4ff; /*   */
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white; /*    . */
    font-weight: bold; /*    . */
}
