/* FAQ   */
.FAQ_wrappage {
    width: 950px;
    margin: 0 auto; /*     */
    text-align: center; /*       */  
}

.title{
  font-weight: bold;
  margin-left: 10px;
}

.box {
  margin-top: 20px;
  position: relative;
  width: 100%; /*    */
  height: 90px; /*    */
  background-color: #d3d3d348; /*    */
  padding: 20px; /*     */
  overflow-x: auto; /*    */
}

.icon_wrap {
  position: absolute;
  top: 50%; /*      */
  left: 50%; /*      */
  transform: translate(-50%, -50%); /*  /   */
  text-align: center; /*     */
  white-space: nowrap; /*    */
}

.ico_01{
  display: inline-block; /*   */
  margin-right: 10px; /*     */
  border:1px solid rgba(211, 211, 211, 0.144);
}

.ico_01 img{
  height: 76px;
}

.FIND_search_box{
  margin-top: 100px;
  margin-top: 20px;
  position: relative;
  width: 100%; /*    */
  height: 80px; /*    */
  background-color: #d3d3d348; /*    */
  padding: 20px; /*     */
  border:1px solid lightgray;
}

.FIND_search{
  margin-right: 50%; /*    */
}

.FIND_search_input {
  width: 350px;
  height: 35px; 
  padding: 10px;
  border:1px solid lightgray;
  border-radius:4px;
  font-size: 13px;
}

.FIND_search_button {
  background-color: rgba(0, 0, 0, 0.767);
  color: white;
  font-size: 14px;
  border: none;
  border-radius: 4px;
  padding: 6px 25px;
  cursor: pointer;
  margin-left: 5px;
}

.FIND_search-label {
  align-self: flex-start; /*    */
  margin-right: 10px; /*    */
}

.FIND_help_wrap {
  margin-left: 55%; /*   */
  margin-top:-40px;
  font-size: 10px;
  font-weight: bold;
}

.test1{
  display: flex;
}
.test1 > p:nth-of-type(1){
  margin-left: 40px;
  font-weight: bold;
}
.test1 > p:nth-of-type(2){
  margin-left: 300px;
  font-weight: bold;
}

/*  Accordion  */
.accordian_all{
  margin-top: 50px;;
}

  .accordian_all p{
    display: block;
    margin-left:20px;
  }
  /*  Accordion.Item   */
  .header { 
    font-weight: bold; /*   */
    cursor: pointer; /*    */
  }
  

  /*  Accordion.Item   */
  .Accordion.Header.active {
    background-color: white; /*    */

  }
  
  /* Accordion.Item ()  */
  .Accordion.Body {
    background-color: #ffffff; /*   */
    padding: 15px; /*   */
    border: 1px solid #dee2e6; /*   */
    border-top: none; /*     */
   
  }
  
  /*     */
  .Accordion.Body a {
    color:blanchedalmond; /*   */
    text-decoration: none; /*   */
  }
  
  .Accordion.Body a:hover {
    text-decoration: underline; /*     */
  }
  
  /*      */
  .Accordion.Body br {
    margin-bottom: 10px; /*     */
    
  }

  .FAQ_page {
    display: flex;
    justify-content: center;
    margin-top: 30px;
  }
  
  .FAQ_page .pageNumber1 {
    border:none;
    margin-right: 15px;
    cursor: pointer;
    background-color: #fff;
  }

  