/* Calender1.css */
.calendarwrap{
  width: 950px;
  margin: 0 auto; /*     */
  text-align: center; /*       */
  margin-bottom: 100px;
}

.calendarContainer {
  width: 100%;
  padding: 10px;
  margin: 0 auto;

}

.calendarHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.calendarHeader h2 {
  font-size: 20px;
  margin: 0;
}

.calendarHeader button {
  background-color: #f0f0f0;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
  font-size: 16px;
}




.calendarDays {
  display: flex;
  justify-content: space-around;
  margin-bottom: 15px;
  text-align: center;
  padding: 5px;
}

.calendarDays div{
  cursor: pointer;
}



.calendarToday {
  background-color: #f0f0f0;
}

.selectedDate {
  padding: 0 10px;
  box-sizing: border-box;
  background-color: black;
  color: white;
}

